import "./styles.css";
// import Seat from "./components/Seat";
import SeatClass from "./components/SeatClass";
import React, { useState, useEffect } from "react";
import { Typography, Button, TextField, Box } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import SeatData from "./data.js";
import AppContext from "./contexts/AppContexts";

const useStyles = makeStyles({
  App: {
    width: "100%"
  }
});

export default function App() {
  const classes = useStyles();
  const [seatsData, setSeatsData] = useState([]);
  const [seatCount, setSeatCount] = useState(1);

  const [selectedSeats, setSelectedSeats] = useState([]);

  const [totalAmount, setTotalAmount] = useState(0);

  const sortByRows = () => {
    let seatSorted = [
      {
        seatClass: "Club",
        priceText: "Rs. 236",
        rows: {}
      },
      {
        seatClass: "Executive",
        priceText: "Rs. 236",
        rows: {}
      }
    ];
    seatSorted.forEach((sortItem) => {
      SeatData.map((item) => {
        if (item.seatClass === sortItem.seatClass) {
          if (!(item.seatRow in sortItem.rows)) {
            sortItem.rows[item.seatRow] = [];
          }
          sortItem.rows[item.seatRow].push(item);
        }
      });
    });
    console.log("seat sorted ---", seatSorted);
    return seatSorted;
  };

  const handleSeatCountChange = (value) => {
    setSeatCount(value);
  };

  const selectSeatCallback = (obj) => {
    if (obj.isSelected) {
      //When checked
      let filteredSeats = selectedSeats.length
        ? selectedSeats.filter((seat) => seat.guid === obj.guid)
        : [];
      if (filteredSeats.length === 0) {
        setSelectedSeats([...selectedSeats, obj]);
        setTotalAmount(totalAmount + parseInt(obj.price));
      }
    } else {
      //When not checked
      let filteredSeats = selectedSeats.length
        ? selectedSeats.filter((seat) => seat.guid !== obj.guid)
        : [];
      setTotalAmount(totalAmount - parseInt(obj.price));
      setSelectedSeats(filteredSeats);
    }
  };

  const removeAllSelected = (obj) => {
    selectedSeats.forEach((selectedSeat) => {
      let _orgSeatData = seatsData.filter(
        (item) => item.seatClass === selectedSeat.seatClass
      );
      Object.keys(_orgSeatData[0].rows).forEach((key) => {
        if (key === selectedSeat.seatRow) {
          let rowItem = _orgSeatData[0].rows[key].filter(
            (rowItem) => rowItem.guid === selectedSeat.guid
          );
          rowItem[0].isSelected = false;
        }
      });
    });

    console.log("modifiedSeatData----", seatsData);
    setSeatsData(seatsData);
    selectedSeats.splice(0, selectedSeats.length);
    setSelectedSeats(selectedSeats);
    setTotalAmount(0);
    console.log("selectedSeats Data", selectedSeats);
    console.log("totalAmount ----", totalAmount);
  };

  const setTotalAmountContextFn = (amt) => {
    setTotalAmount(amt);
  };

  useEffect(() => {
    setSeatsData(sortByRows());
    console.log("seatsdata---", seatsData);
  }, []);

  return (
    <AppContext.Provider
      value={{
        totalAmount,
        setTotalAmountContextFn,
        selectedSeats,
        removeAllSelected
      }}
    >
      <div className={classes.App}>
        {/* <Seat /> */}
        {seatsData && seatsData.length ? (
          <SeatClass
            seatsData={seatsData}
            seatCount={seatCount}
            selectedSeats={selectedSeats}
            selectSeatCallback={selectSeatCallback}
            removeAllSelected={removeAllSelected}
            style={{ marginTop: "10px" }}
          />
        ) : (
          "Error while fetching the data"
        )}

        <Typography variant="body2" gutterBottom>
          How many seats?
        </Typography>
        <TextField
          style={{ width: "75px" }}
          id="seats-to-book"
          label=""
          value={seatCount}
          type="number"
          InputLabelProps={{
            shrink: true
          }}
          error={seatCount === "0"}
          helperText={seatCount === "0" ? "Invalid entry" : ""}
          onChange={(e) => handleSeatCountChange(e.target.value)}
          inputProps={{ min: "0", max: "10", step: "1" }}
        />
        <Box>
          <Button
            style={{ marginTop: "20px" }}
            variant="contained"
            color="primary"
            disabled={seatCount === "0"}
          >
            Pay Rs.{totalAmount}
          </Button>
        </Box>
      </div>
    </AppContext.Provider>
  );
}
