import React, { useState, useContext } from "react";
import { makeStyles } from "@material-ui/styles";
import clsx from "clsx";
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import "./seat.css";
import AppContext from "../contexts/AppContexts";

const useStyles = makeStyles({
  root: {
    position: "relative",
    padding: "6px 9px 6px 9px",
    "&:hover": {
      backgroundColor: "transparent"
    }
  },
  icon: {
    borderRadius: 3,
    width: 25,
    height: 25,
    boxShadow:
      "inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)",
    backgroundColor: "#f5f8fa",
    backgroundImage:
      "linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))",

    "$root.Mui-focusVisible &": {
      outline: "2px auto rgba(19,124,189,.6)",
      outlineOffset: 2
    },
    "input:hover ~ &": {
      backgroundColor: "#ebf1f5"
    },
    "input:disabled ~ &": {
      background: "rgba(206,217,224,.5)"
    }
  },

  checkedIcon: {
    backgroundColor: "#137cbd",
    backgroundImage:
      "linear-gradient(180deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,0))",

    "&:before": {
      boxShadow:
        "inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)",
      display: "block",
      width: 25,
      borderRadius: 3,
      height: 25,
      backgroundColor: "#7FFF00",
      content: '""'
    },
    "input:hover ~ &": {
      backgroundColor: "#106ba3"
    }
  }
});

const CheckBoxUI = (props) => {
  const classes = useStyles(props);
  return (
    <Checkbox
      className={classes.root}
      icon={<span className={classes.icon} />}
      checkedIcon={<span className={clsx(classes.icon, classes.checkedIcon)} />}
      {...props}
    />
  );
};

const useSeatStyles = makeStyles({
  checkboxLabel: {
    position: "relative",
    marginRight: "2px",

    "&.seat-checkbox-hide": {
      visibility: "hidden"
    }
  }
});

export default function Seat(props) {
  const classes = useSeatStyles(props);

  // const context = useContext(AppContext);

  const { seatObj, selectSeatCallback, seatCount, selectedSeats } = props;

  const [checked, setChecked] = useState(seatObj.isSelected);

  const [visible, setVisible] = useState(seatObj.isNotaSeat);

  const [seatCountValid, setSeatCountValid] = useState(props.seatCount > 0);

  const handleChange = (e, removeAllSelected, setTotalAmountContextFn) => {
    if (e.target.checked && Number(seatCount) === selectedSeats.length) {
      // context.removeAllSelected();
      // context.setTotalAmountContextFn(0);
      removeAllSelected();
      setTotalAmountContextFn();
    }
    if (
      e.target.checked &&
      selectedSeats.length &&
      selectedSeats[0].seatClass !== seatObj.seatClass
    ) {
      // context.removeAllSelected();
      // context.setTotalAmountContextFn(0);
      removeAllSelected();
      setTotalAmountContextFn();
    }
    setChecked(e.target.checked);
    seatObj.isSelected = e.target.checked;

    selectSeatCallback(seatObj);
  };

  return (
    <AppContext.Consumer>
      {({
        setTotalAmountContextFn,
        totalAmount,
        selectedSeats,
        removeAllSelected
      }) => (
        <div>
          <FormControlLabel
            value="top"
            className={clsx(
              classes.checkboxLabel,
              "seat-checkbox",
              visible && "seat-checkbox-hide"
            )}
            control={
              <CheckBoxUI
                color="primary"
                checked={checked}
                disabled={!seatCountValid ? true : !seatObj.isAvailable}
                onChange={(e) =>
                  handleChange(e, removeAllSelected, setTotalAmountContextFn)
                }
              />
            }
            label={seatObj.seatNumber}
          />
        </div>
      )}
    </AppContext.Consumer>
  );
}
