import React from "react";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import Row from "./Row";

export default function SeatClass(props) {
  const { seatsData } = props;

  const Rows = (props) => {
    let aRow = [];
    Object.keys(props.rows).forEach((key) => {
      aRow.push(<Row rowName={key} seats={props.rows[key]} {...props} />);
    });
    return aRow;
  };

  return seatsData.map((data) => {
    return (
      <React.Fragment>
        <Typography variant="button" display="block">
          {data.seatClass} -{" "}
          <span style={{ textTransform: "none" }}>{data.priceText}</span>
        </Typography>
        <Divider style={{ marginBottom: "10px" }} />
        <Rows rows={data.rows} {...props} />
      </React.Fragment>
    );
  });
}
